@import url(https://fonts.googleapis.com/css?family=Cedarville+Cursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {

  --dvblack: black;
  --dvdarkgrey: #383838;
}

.App {
  text-align: center;
  background-color: #4D753B;
}

.btn:focus,.btn:active {
  box-shadow: none !important;
}

.navbar {
  min-height: 6vh;
  background-color: #4D753B;
}

.navbar-brand {
  color: white !important;
}

.navbar-button {
  background-color: #4D753B !important;
  color: white;
  border: none !important;
}

html {
  scroll-behavior: smooth;
}
.chapter-page-comp {
  align-content: center;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.chapter-title {
  color: white;
  text-align: center;
  font-size: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin-top: 30px;
}

.chapter-caption {
  color: white;
  font-size: 20px;
}

.chapter-visualisation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-content: center;
}

.chapter-help-section {
  margin-top: 20px;
  color: white;
}
.text-chapter-page-comp {
  align-content: center;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.text-chapter-page-title {
  color: white;
  text-align: center;
  font-size: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  margin-top: 30px;
}

.text-chapter-writing {
  color: white;
  font-size: 26px;
}

.text-chapter-help-section {
  margin-top: 20px;
  color: white;
}
.item-card {
  height: 150px;
  width: 150px;
  margin: 10px;
  background-color: grey;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  color: white;
  font-size: 18px;
  border: 1px black solid;
}

.item-card2 {
  height: 150px;
  width: 150px;
  margin: 10px;
  background-color: grey;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  color: white;
  font-size: 15px;
  border: 1px black solid;
}

.grid-style{
  margin: 0px;
  padding: 0px;
  display: flex;
  font-size: 16px;
}

.icon-style {
  height: 70px;
  width: 70px;
}

.grid-holder{
  width: 528px;
  height: 528px;
  background-color: white;
  border: 4px black solid;
  padding: 5px;
  align-self: center;
}

.icon-holder {
  margin-top: 2px;
}

.icon-style {
  margin: 10px;
  font-size: 25px !important;
  color: white;
}

.home-page-comp {
  align-content: center;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.page-title {
  color: white;
  text-align: center;
  font-size: 80px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  margin-top: 30px;
}

.introduction {
  color: white;
  font-size: 24px;
}

.home-page-icon {
  height: 200px;
  width: 200px;
}
