.item-card {
  height: 150px;
  width: 150px;
  margin: 10px;
  background-color: grey;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  color: white;
  font-size: 18px;
  border: 1px black solid;
}

.item-card2 {
  height: 150px;
  width: 150px;
  margin: 10px;
  background-color: grey;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
