.grid-style{
  margin: 0px;
  padding: 0px;
  display: flex;
  font-size: 16px;
}

.icon-style {
  height: 70px;
  width: 70px;
}

.grid-holder{
  width: 528px;
  height: 528px;
  background-color: white;
  border: 4px black solid;
  padding: 5px;
  align-self: center;
}

.icon-holder {
  margin-top: 2px;
}

.icon-style {
  margin: 10px;
  font-size: 25px !important;
  color: white;
}
